import { Col, Row } from "antd";
import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Fn } from "../../../../helper/function";
import { TaskProjectManageService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { Box, MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { UIFormatDateVNComponent } from "../../../core/UI_FormatComponent";

export default function CloneComponent() {
    let projectKey = useParams().key;

    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const [otp, setOtp] = useState('');
    const [projectName, setProjectName] = useState('');
    const [projectContent, setProjectContent] = useState('');
    const [projectTag, setProjectTag] = useState('');
    const [projectParent, setProjectParent] = useState('');
    const [projectStatus, setProjectStatus] = useState(0);
    const [sprints, setSprints] = useState([]);

    const [settingType1, setSettingType1] = useState([]);
    const [settingType2, setSettingType2] = useState([]);
    const [settingType3, setSettingType3] = useState([]);
    const [settingType4, setSettingType4] = useState([]);
    const [settingType5, setSettingType5] = useState([]);
    const [settingType6, setSettingType6] = useState([]);
    const [settingType7, setSettingType7] = useState([]);
    const [settingType8, setSettingType8] = useState([]);

    function handleClickGetClone() {
        setIsLoading(true);
        TaskProjectManageService.GetClone(projectKey)
            .then((res: any) => {
                if (res.isSuccess) {
                    let projectDetail = res.data.project;
                    setProjectName(projectDetail.name);
                    setProjectContent(projectDetail.content);
                    setProjectTag(projectDetail.tag.toString(","));
                    setProjectParent(projectDetail.parentProject?.name);
                    setProjectStatus(projectDetail.status.value);
                    setSprints(res.data.sprints);

                    for (let index = 0; index < res.data.statusSettings.length; index++) {
                        const element = res.data.statusSettings[index];
                        if (element.type === 1) {
                            setSettingType1(element.datas);
                        }

                        if (element.type === 2) {
                            setSettingType2(element.datas);
                        }

                        if (element.type === 3) {
                            setSettingType3(element.datas);
                        }

                        if (element.type === 4) {
                            setSettingType4(element.datas);
                        }

                        if (element.type === 5) {
                            setSettingType5(element.datas);
                        }

                        if (element.type === 6) {
                            setSettingType6(element.datas);
                        }

                        if (element.type === 7) {
                            setSettingType7(element.datas);
                        }

                        if (element.type === 8) {
                            setSettingType8(element.datas);
                        }
                    }
                }
            })
            .finally(() => setIsLoading(false));
    }

    function handleConfirmClone() {
        let body = {
            otp: otp
        };
        setIsLoading(true);
        TaskProjectManageService.ProcessClone(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    navigate(`/tools/tasks/projects/${rs.data.projectKeyNew}`);
                }
            })
            .finally(() => setIsLoading(false));
    }

    function RenderSprints() {
        return sprints.map((data: any, index: number) =>
            <Box key={index}>
                <p>
                    <strong>Giai đoạn {index + 1}: </strong> {data.name}
                </p>
                <p>
                    <strong>Trạng thái: </strong> {data.status.value}
                </p>
                <p>
                    <strong>Thời gian: </strong>
                    <UIFormatDateVNComponent value={data.dateFrom} /> -
                    <UIFormatDateVNComponent value={data.dateTo} />
                </p>
            </Box>
        )
    }

    function RenderList(datas: any) {
        return datas.map((data: any, index: number) => {
            if (data.isNew !== null) {
                return <li key={index}>
                    {data.value}
                    - Trạng thái mới: <strong>{data.isNew ? "True" : "False"}</strong>
                    - Trạng thái đóng: <strong>{data.isClosed ? "True" : "False"}</strong>
                </li>;
            } else {
                return <li key={index}>{data.value}</li>;
            }
        })
    }

    return (
        <MyBox
            isLoading={isLoading}
            title="Clone dự án">
            <Box showBox={false}>
                <MyButton
                    type="primary"
                    isLoading={isLoading}
                    onClick={handleClickGetClone}
                    value="Get data clone" />
            </Box>
            <Box showBox={false}>
                <Row gutter={24}>
                    <Col span={12}>
                        <MyFormItem.Input
                            label="Nhập OTP"
                            value={otp}
                            setValue={setOtp} />
                    </Col>
                    <Col span={12}>
                        <MyButton
                            type="primary"
                            isLoading={isLoading}
                            onClick={handleConfirmClone}
                            value="Xác nhận clone dự án" />
                    </Col>
                </Row>
            </Box>
            <Box>
                <h2>Chi tiết dự án</h2>
                <Row gutter={12}>
                    <Col span={12}>
                        <strong>Tên dự án: </strong>{projectName}
                    </Col>

                    <Col span={12}>
                        <strong>Tag: </strong> {projectTag}
                    </Col>

                    <Col span={24}>
                        <strong>Nội dung: </strong>{projectContent}
                    </Col>

                    <Col span={12}>
                        <strong>Dự án cha: </strong> {projectParent}
                    </Col>

                    <Col span={12}>
                        <strong>Trạng thái: </strong> {projectStatus}
                    </Col>
                </Row>
            </Box>

            <Box>
                <h2>Danh sách các giai đoạn dự án</h2>
                {RenderSprints()}
            </Box>

            <Box>
                <h2>Danh sách cài đặt dự án</h2>
                <Row gutter={12}>
                    <Col span={24}>
                        <h3>Danh sách trạng thái dự án</h3>
                        <ul>
                            {RenderList(settingType1)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách trạng thái giai đoạn</h3>
                        <ul>
                            {RenderList(settingType2)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách trạng thái công việc</h3>
                        <ul>
                            {RenderList(settingType3)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách trạng thái vấn đề</h3>
                        <ul>
                            {RenderList(settingType4)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách độ ưu tiên</h3>
                        <ul>
                            {RenderList(settingType5)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách mức độ ảnh hưởng</h3>
                        <ul>
                            {RenderList(settingType6)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách Tag</h3>
                        <ul>
                            {RenderList(settingType7)}
                        </ul>
                    </Col>

                    <Col span={24}>
                        <h3>Danh sách loại vấn đề</h3>
                        <ul>
                            {RenderList(settingType8)}
                        </ul>
                    </Col>
                </Row>
            </Box>
        </MyBox >
    )
}